<template>
  <div class="dataManage admin-layout">
    <div class="header">
      <el-form :model="form" ref="form" inline>
        <el-form-item label="字典名称" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="btns mb20">
      <el-button type="primary" @click="addType">新增类型</el-button>
      <el-button type="success" @click="editType">编辑类型</el-button>
      <el-button type="danger" @click="delType">删除类型</el-button>
      <el-button type="primary" @click="add">新增字典</el-button>
    </div>
    <div class="main">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-card>
            <div slot="header">
              <el-input v-model="searchCode" @keypress.enter="getTypeList">
                <el-button
                  slot="append"
                  icon="el-icon-search"
                  @click="getTypeList"
                ></el-button>
              </el-input>
            </div>
            <el-tree
              :data="treeData"
              :props="defaultProps"
              node-key="id"
              :current-node-key="curNode.id"
              highlight-current
              @node-click="handleNodeClick"
            ></el-tree>
          </el-card>
        </el-col>
        <el-col :span="18">
          <el-card>
            <div slot="header">
              {{ curNode.name || "未选择节点" }}
            </div>
            <el-table border :data="tableData" ref="tableData">
              <el-table-column
                label="序号"
                type="index"
                align="center"
                width="50px"
              />
              <el-table-column label="名称" prop="name" align="center" />
              <el-table-column label="值" prop="value" align="center" />
              <el-table-column label="类型" prop="code" align="center" />
              <el-table-column label="类型" prop="code" align="center">
                <template slot-scope="{ row }">
                  <el-button type="text" @click="check(row)">查看</el-button>
                  <el-button type="text" @click="edit(row)">编辑</el-button>
                  <el-button type="text" @click="del(row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <!-- <pagination
              :total="total"
              :page.sync="form.page"
              :limit.sync="form.pageSize"
              @pagination="getList"
            /> -->
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-dialog :visible.sync="typeShow" :title="typeTitle" width="400px">
      <el-form :model="typeObj" label-width="100px">
        <el-form-item label="类型名称" prop="name">
          <el-input :disabled="typeTitle == '查看类型'" v-model="typeObj.name"></el-input>
        </el-form-item>
        <el-form-item label="类型编码" prop="code">
          <el-input :disabled="typeTitle == '查看类型'" v-model="typeObj.code"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button v-if="typeTitle == '查看类型'" type="primary" @click="typeShow = false"
          >知道了</el-button
        >
        <template v-else>
          <el-button type="primary" @click="confirmType">确认</el-button>
          <el-button>取消</el-button>
        </template>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="show" :title="title" width="400px">
      <el-form :model="obj" label-width="100px">
        <el-form-item label="名称" prop="name">
          <el-input :disabled="title == '查看'" v-model="obj.name"></el-input>
        </el-form-item>
        <el-form-item label="值" prop="value">
          <el-input :disabled="title == '查看'" v-model="obj.value"></el-input>
        </el-form-item>
        <el-form-item label="所属类型" prop="code">
          <el-input :disabled="title == '查看'" v-model="obj.code"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button v-if="title == '查看'" type="primary" @click="show = false"
          >知道了</el-button
        >
        <template v-else>
          <el-button type="primary" @click="confirm">确认</el-button>
          <el-button>取消</el-button>
        </template>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'DataManage',
  data () {
    return {
      form: {
        code: '',
        name: ''
        // page: 1,
        // pageSize: 10
      },
      total: 0,
      tableData: [
        {
          value: '值',
          name: '名称',
          code: 'yesOrNo',
          id: '11'
        }
      ],
      treeData: [
        {
          id: '1',
          name: '是否',
          code: 'yesOrNo'
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      curNode: {},
      // 字典弹出框属性
      show: false,
      title: '新增',
      obj: {
        id: '',
        code: '',
        name: '',
        value: ''
      },
      // 类型弹出框属性
      typeShow: false,
      typeTitle: '新增',
      typeObj: {
        id: '',
        code: '',
        name: ''
      },
      // 类型搜索
      searchCode: ''
    }
  },
  methods: {
    search () {
      // this.form.page = 1
      this.getList()
    },
    reset () {
      this.$refs.form.resetFields()
    },
    getTypeList () {
      console.log('getTypeList')
      // this.searchCode
    },
    getList () {
      console.log('getList')
    },
    add () {
      if (this.curNode) {
        this.obj.id = ''
        this.obj.name = ''
        this.obj.value = ''
        this.obj.code = this.curNode.code
        this.title = '新增'
        this.show = true
      } else {
        this.$message.error('请选择类型节点')
      }
    },
    check (row) {
      this.obj.id = row.id
      this.obj.name = row.name
      this.obj.code = row.code
      this.obj.value = row.value
      this.title = '查看'
      this.show = true
    },
    edit (row) {
      this.obj.id = row.id
      this.obj.name = row.name
      this.obj.code = row.code
      this.obj.value = row.value
      this.title = '编辑'
      this.show = true
    },
    del (row) {
      console.log('删除', row)
    },
    addType () {
      this.typeObj.id = ''
      this.typeObj.name = ''
      this.typeObj.code = ''
      this.typeTitle = '新增类型'
      this.typeShow = true
    },
    checkType () {
      if (this.curNode) {
        this.typeObj.id = this.curNode.id
        this.typeObj.name = this.curNode.name
        this.typeObj.code = this.curNode.code
        this.typeTitle = '查看类型'
        this.typeShow = true
        console.log('查看')
      } else {
        this.$message.error('请选择要查看的节点')
      }
    },
    editType (row) {
      if (this.curNode) {
        this.typeObj.id = this.curNode.id
        this.typeObj.name = this.curNode.name
        this.typeObj.code = this.curNode.code
        this.typeTitle = '编辑类型'
        this.typeShow = true
        console.log('编辑类型')
      } else {
        this.$message.error('请选择要编辑的节点')
      }
    },
    delType (row) {
      if (this.curNode) {
        this.curNode = {}
      }
      console.log('删除', row)
    },
    handleNodeClick (data) {
      this.curNode = data
      this.form.page = 1
      this.getList()
    },
    isArray (obj) {
      return Object.prototype.toString.call(obj) === '[object Array]'
    },
    confirm () {
      if (this.title === '新增') {
        console.log('新增', this.obj)
      } else if (this.title === '编辑') {
        console.log('编辑', this.obj)
      }
      this.show = false
    },
    confirmType () {
      if (this.title === '新增类型') {
        this.treeData.push(Object.assign({}, this.typeObj))
      } else if (this.title === '编辑类型') {
        this.curNode.id = this.typeObj.id
        this.curNode.name = this.typeObj.name
        this.curNode.code = this.typeObj.code
      }
      this.typeShow = false
    }
  }
}
</script>
<style lang="scss" scoped>
.dataManage {
  .btns {
    margin-bottom: 20px;
  }
}
</style>
